html {
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

* {
    margin: 0;
    padding: 0;
    /* box-sizing: border-box; */
}

body {
    color: #4F6367;
    background-color: #0a5a72;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    font-size: 15px;
    line-height: 1.25;
    min-height: 100vh;
}

img {
    width: 300px;
}

a {
    color: black;
}


.divider>article {
    border-bottom: 1px dashed rgb(175, 122, 59);
    padding: 5px 0;
}

.divider>article:last-of-type {
    border-bottom: none;
}

.divider>article:first-of-type {
    padding: 10px 0;
}

/* This takes precedence over above */
a {
    color: yellowgreen;
}

a:hover {
    text-decoration: none;
}

h1,
h2 {
    font-family: Caveat, cursive;
    font-weight: 400;
    margin: 0;
}

h1 {
    font-size: 100px;
    line-height: 1;
}

h2 {
    font-size: 45px;
}

.container {
    max-width: 900px;
    margin: 0 auto;
    /* padding: 30px 0; */
    padding: 0;
}

/** Specific segments */
header {
    background-color: #0a5a72;
    color: #F7FFf7;
}

main {
    background-color: #ffffff;
}

section a {
    color: green;
}

.container a {
    color: orange;
}

section p a {
    color: pink;
}

.container p a {
    color: red;
}

.lorem-ipsum p {
    color: black;
    padding: 20px;
    font-size: 12px;
    font-size: 16px;
    /* This style takes precedence */
}

.lorem-ipsum {
    overflow: hidden;
}

.role-item img {
    margin: 0 10px 0 0;
}

.role-item h3 {
    margin-top: 0;
}

.clear {
    clear: both;
}

.cleared {
    overflow: hidden;
    /* OR */
    /*overflow: auto;*/
}

.nav-menu .btn {
    color: white;
    background: darkblue;
    text-decoration: none;
    padding: 8px;
    border-radius: 4px;
    display: inline-block;
}

.nav-menu .btn:hover {
    background: rgba(0, 0, 139, 0.8)
}

.contact-list {
    list-style-type: none;
    padding: 0;
}

.contact-list a {
    padding: 15px;
    display: inline-block;
}

footer {
    background-color: #0a5a72;
    color: #F7FFf7;
}

h3 {
    margin-bottom: 0;
}

.role-details h3+p {
    font-style: italic;
}

h3~p {
    margin: 0;
}

/** Responsive design */
@media screen and (min-width: 540px) {

    header,
    footer {
        text-align: center;
    }

    .role-item {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 20px;
    }

    img {
        float: left;
        margin: 10px;
    }

    .contact-list {
        display: flex;
        justify-content: center;
    }
}

@media screen and (max-width: 539px) {
    h1 {
        font-size: 75px;
        line-height: 0.9;
        margin-bottom: 20px;
    }

    h2 {
        line-height: 1;
    }

    .contact-list a {
        padding: 5px;
    }
}

